html {
  font-size: 100%; /* 16px */
}

body {
  display: grid;
  height: 100%;
  width: 100%;
  color: #24292e;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

img {
  pointer-events: none;
}

#App {
  -webkit-tap-highlight-color: transparent;
}

@media screen and (max-width: 768px) {
  #home {
    margin: 0 auto;
    width: 100%;
    position: relative;
    overflow: hidden;
  }

  #home #header {
    max-width: 960px;
    text-align: center;
    padding: 30px 0;
    background-color: rgba(253, 236, 224, 255);
  }

  #main {
    display: flex;
    align-items: center;
    position: relative;
  }

  #main img {
    width: 100%;
    height: 650px;
    object-fit: cover;
  }

  .menu-nav {
    position: absolute;
    top: 0;
    left: 0;
  }

  #main ul li {
    padding: 15px 15px 3px 20px;
    position: relative;
  }

  #main ul li::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: -100%; /* ÂàùÊúüÁä∂ÊÖã„ÅßÂ∑¶„Å´Èö†„Åô */
    width: 100%;
    height: 2px;
    background-color: #a53342;
    transition: left 0.4s ease-in-out; /* „Ç¢„Éã„É°„Éº„Ç∑„Éß„É≥Ë®≠ÂÆö */
  }

  #main ul li:hover::before {
    left: 0; /* „Éõ„Éê„ÉºÊôÇ„Å´Â∑¶„Åã„ÇâÂè≥„Å∏Ë°®Á§∫ */
  }

  #main ul li a span {
    font-size: 1.2rem;
    font-weight: 500;
    line-height: 2.5rem;
    color: rgb(80, 80, 80);
    overflow: hidden;
    margin: 0;
    padding: 0;
    white-space: nowrap; /*Á©∫ÁôΩ„Åß„ÇÇÊîπË°å„Åï„Çå„Å™„ÅÑ*/
  }

  hooter {
    display: flex;
    justify-content: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    padding-bottom: 10px;
  }

  hooter .button {
    display: flex;
    flex-wrap: wrap;
    height: 60px;
    width: 60px;
    float: left;
    margin: 0 10px;
    cursor: pointer;
    border-radius: 50px;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
    overflow: hidden;
  }

  hooter .button:hover {
    width: 200px;
  }

  hooter .button:nth-child(1):hover .icon {
    background-color: #9dceec;
  }

  hooter .button:nth-child(2):hover .icon {
    background-color: #d99ae6;
  }

  hooter .button .icon {
    display: inline-block;
    height: 60px;
    width: 60px;
    border-radius: 50px;
    box-sizing: border-box;
    text-align: center;
    line-height: 60px;
    transition: all 0.3s;
  }

  hooter .button .icon i {
    font-size: 25px;
    line-height: 60px;
  }

  hooter .button span {
    font-size: 20px;
    font-weight: 500;
    line-height: 60px;
    margin: auto;
    padding-right: 30px;
  }

  .fa-twitter:before {
    content: "ùïè";
    font-family: unset;
    font-weight: bold;
  }

  .number-spinner-wrap input::-webkit-outer-spin-button,
  .number-spinner-wrap input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .number-spinner-input {
    margin: 0;
  }

  .spinner {
  }

  /* „Çπ„Éî„É≥„Éú„Çø„É≥„ÅÆ„Çπ„Çø„Ç§„É´ */
  .number-spinner-wrap {
    display: flex;
    align-items: center;
    margin-bottom: 20px; /* „É¢„Éê„Ç§„É´„Éá„Éê„Ç§„Çπ„Åß„ÅØ„Çπ„Éö„Éº„Çπ„ÇíËøΩÂä† */
  }

  .spinner {
    width: 40px;
    height: 40px;
    background-color: #8a2be2;
    color: #fff;
    font-size: 24px;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    margin: 0 10px;
    transition: background-color 0.2s;
  }

  .spinner:hover {
    background-color: #6a1e9b;
  }

  .check-label-wrap {
    display: flex;
    justify-content: space-around;
  }

  .check-label-wrap div {
  }

  /* „ÉÅ„Çß„ÉÉ„ÇØ„Éú„ÉÉ„ÇØ„Çπ„ÅÆ„Çπ„Çø„Ç§„É´ */
  .check-label {
    display: flex;
    align-items: center;
    margin: 10px 0; /* „É¢„Éê„Ç§„É´„Éá„Éê„Ç§„Çπ„Åß„ÅØ„Çπ„Éö„Éº„Çπ„ÇíËøΩÂä† */
    cursor: pointer;
  }

  .check-noun {
  }

  .check-verb,
  .check-adj {
    margin-top: 50px;
  }

  input[type="checkbox"] {
    margin-right: 10px;
    appearance: none;
    width: 24px;
    height: 24px;
    border: 2px solid #8a2be2;
    border-radius: 4px;
    cursor: pointer;
  }

  input[type="checkbox"]:checked {
    background-color: #8a2be2;
    border-color: #8a2be2;
    transition: background-color 0.7s;
  }

  input[type="number"] {
    outline: none;
    outline: solid 3px rgb(188, 156, 214);
    box-shadow: 0px 0px 10px 0px #8366ec;
    margin: 0 auto;
  }

  /*
input[type="checkbox"]:hover {
  background-color: #8a2be2;
  transition: background-color 0.8s;
}

input[type="checkbox"]:checked:hover {
  background-color: lightgray;
  transition: background-color 1s;
}
*/
  /* ÈñãÂßã„Éú„Çø„É≥„ÅÆ„Çπ„Çø„Ç§„É´ */
  .start-button {
    padding: 12px 0; /* ‰∏ä‰∏ã„ÅÆ‰ΩôÁôΩ„Çí0„Å´Ë®≠ÂÆö */
    width: 100%; /* ÂπÖ„Çí50%„Å´Ë®≠ÂÆö */
    text-align: center; /* Â∑¶Âè≥‰∏≠Â§Æ„Å´ÈÖçÁΩÆ */
  }

  /* „Éú„Çø„É≥„ÅÆ„Çπ„Çø„Ç§„É´ */
  button {
    padding: 12px 0;
    width: 40%;
    margin: 0 auto;
    background-color: #8a2be2;
    color: #fff;
    font-size: 18px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    overflow: hidden;
  }

  button:hover {
    background-color: #6a1e9b;
    transition: 0.5s;
  }

  .start-button::after,
  .slide-button::after {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    background: #8a2be2;
    width: 100%;
    height: 100%;
    z-index: -1;
    transform: translateX(-100%);
    animation-name: button_slide;
    animation-duration: 0.5s;
    animation-fill-mode: forwards;
  }

  .slide-bar {
  }

  @keyframes button_slide {
    0% {
      transform: translate(-100%, 0);
    }
    100% {
      transform: translate(0, 0);
    }
  }

  /* Êï∞ÈáèÂÖ•Âäõ„Éï„Ç£„Éº„É´„Éâ„ÅÆ„Çπ„Çø„Ç§„É´ */
  input[type="number"] {
    width: 60px;
    padding: 10px;
    font-size: 18px;
    border: 1px solid #ccc;
    border-radius: 5px;
    text-align: center;
    margin-right: 10px; /* „É¢„Éê„Ç§„É´„Éá„Éê„Ç§„Çπ„ÅßÂè≥ÂÅ¥„ÅÆ„Çπ„Éö„Éº„Çπ„ÇíËøΩÂä† */
  }
}
